// Sentra AI SaaS Tool
// This project is a customer support AI tool.
// Users can input ticket text and get predictions for churn, sentiment, category, and priority.
// The landing page should be visually engaging using Magic UI background animations and shadcn/ui components. This is a SaaS product, so the UI should look professional and modern.
// Copilot should assist in creating sections, forms, and layout for this SaaS tool.
//use typing animation as effect for Sentra AI and remove the old line shadow text animation
// Make the entire mode dark mode and in a dark style.
//Change the rainbow button for analyze to interactive hover button
// Use the item from shadcn to display the how we are different section cards
//Change the content of how we are different to include Churn Prediction, Sentiment Analysis, AI Insights and Seamlesss insights.

"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { RetroGrid } from "@/components/ui/retro-grid"
import { SmoothCursor } from "@/components/ui/smooth-cursor"

const comparisonPoints = [
  {
    title: "Churn Prediction",
    description: "AI-powered algorithms predict customer churn risk based on support ticket sentiment and content patterns.",
    icon: "ðŸ“Š",
  },
  {
    title: "Sentiment Analysis",
    description: "Real-time sentiment detection to understand customer emotions and satisfaction levels instantly.",
    icon: "ðŸ˜Š",
  },
  {
    title: "AI Insights",
    description: "Advanced ML models provide actionable insights and recommendations to reduce customer churn.",
    icon: "ðŸ§ ",
  },
  {
    title: "Seamless Insights",
    description: "Seamlessly integrate with your existing support systems for effortless ticket analysis.",
    icon: "âš¡",
  },
]

export default function Home() {
  const [ticketText, setTicketText] = useState("")
  const [displayedTitle, setDisplayedTitle] = useState("")
  const router = useRouter()
  const fullTitle = "Sentra AI"

  useEffect(() => {
    let index = 0
    const interval = setInterval(() => {
      if (index < fullTitle.length) {
        setDisplayedTitle(fullTitle.substring(0, index + 1))
        index++
      } else {
        clearInterval(interval)
      }
    }, 100)
    return () => clearInterval(interval)
  }, [])

  const handleAnalyze = () => {
    if (ticketText.trim()) {
      router.push(`/result?ticket=${encodeURIComponent(ticketText)}`)
    }
  }

  return (
    <div className="w-full overflow-hidden bg-black">
      <SmoothCursor />

      {/* Section 1: Hero */}
      <section className="relative w-full min-h-screen flex items-center justify-center overflow-hidden">
        <RetroGrid angle={65} cellSize={60} opacity={0.2} />
        <div className="relative z-10 flex flex-col items-center justify-center gap-8 px-4 text-center">
          <div className="h-20 flex items-center justify-center">
            <h1 className="text-6xl md:text-7xl font-bold text-white min-h-20">
              {displayedTitle}
              {displayedTitle.length < fullTitle.length && (
                <span className="animate-pulse">|</span>
              )}
            </h1>
          </div>
          <p className="max-w-2xl text-lg md:text-xl text-gray-400">
            Intelligent customer support analysis powered by advanced AI. Predict churn, analyze sentiment, and prioritize tickets automatically.
          </p>
        </div>
      </section>

      {/* Section 2: Ticket Input */}
      <section className="w-full py-20 px-4 bg-black border-t border-gray-800">
        <div className="max-w-3xl mx-auto">
          <h2 className="text-4xl font-bold text-center mb-4 text-white">
            Analyze Your Ticket
          </h2>
          <p className="text-center text-gray-400 mb-12">
            Paste your customer support ticket below and let our AI provide insights.
          </p>

          <div className="bg-gray-900 border border-gray-800 rounded-lg p-8">
            <div className="space-y-6">
              <div>
                <label htmlFor="ticket" className="block text-sm font-medium mb-2 text-white">
                  Support Ticket
                </label>
                <textarea
                  id="ticket"
                  value={ticketText}
                  onChange={(e) => setTicketText(e.target.value)}
                  placeholder="Paste your customer support ticket here..."
                  className="w-full h-32 p-4 border border-gray-700 rounded-lg bg-gray-800 text-white placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent resize-none transition-all"
                />
              </div>

              <div className="flex justify-center">
                <button
                  onClick={handleAnalyze}
                  disabled={!ticketText.trim()}
                  className="group relative px-8 py-3 text-white font-semibold rounded-lg bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-300 overflow-hidden"
                >
                  <span className="relative z-10 flex items-center gap-2">
                    Analyze Ticket
                    <span className="group-hover:translate-x-1 transition-transform">â†’</span>
                  </span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Section 3: How We Are Different */}
      <section className="w-full py-20 px-4 bg-black border-t border-gray-800">
        <div className="max-w-6xl mx-auto">
          <h2 className="text-4xl font-bold text-center mb-4 text-white">
            How We Are Different
          </h2>
          <p className="text-center text-gray-400 mb-12">
            Sentra AI stands out with powerful AI-driven features designed for modern support teams.
          </p>

          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6">
            {comparisonPoints.map((point, index) => (
              <div
                key={index}
                className="group relative p-6 rounded-lg border border-gray-800 bg-gray-900 hover:border-blue-600 hover:shadow-lg hover:shadow-blue-500/20 transition-all duration-300 cursor-pointer"
              >
                <div className="text-4xl mb-4">{point.icon}</div>
                <h3 className="text-xl font-semibold mb-3 text-white group-hover:text-blue-400 transition-colors">
                  {point.title}
                </h3>
                <p className="text-gray-400 group-hover:text-gray-300 transition-colors text-sm leading-relaxed">
                  {point.description}
                </p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Footer */}
      <section className="w-full py-12 px-4 bg-black border-t border-gray-800">
        <div className="max-w-6xl mx-auto text-center">
          <p className="text-gray-500">
            Â© 2026 Sentra AI. All rights reserved.
          </p>
        </div>
      </section>
    </div>
  )
}
